.header {
    grid-area: h;
    background-color: black;
    width: 100%;
    line-height: 60px;
}

.logo {
    width: 100px;
    margin-left: 15px;
    height: 60px;
}

.loginBlock{
    color: white;
    height: 60px;
    margin-right: 20px;
    float: right;
}

.loginBlock a {
    color: white;
    text-decoration: none;
    height: 60px;
    float: right;
}

.headerAvatar {
    height: 20px;
    width: 20px;
    border-radius: 10px;
    line-height: 60px;
}
