.nav {
    grid-area: n;
    background-color: #404050;
    display: flex;
    flex-direction: column;
    padding-top: 20px;
}

.item {
    display: inline-flex;
    color: white;
    text-decoration: none;
    line-height: 35px;
    text-align: left;
    vertical-align: center;
    padding-left: 20px;
    background: #404050;
    cursor: default;
}

.active {
    display: inline-flex;
    color: white;
    text-decoration: none;
    line-height: 35px;
    text-align: left;
    padding-left: 20px;
    background: #505060;
    cursor: default;
}